<template>
	<div class="home">
		<!-- 头部 -->
		<div class="header">
			<!-- 地图 -->
			<div class="map" @click="mapCity">{{city}}</div>
			<div class="searchBox">
				<van-icon name="search" size="16" />
			</div>
			<!-- 搜索框 -->
			<router-link to="/home/search" class="search">
				<div>搜索商品</div>
			</router-link>
		</div>
		<!-- 轮播 -->
		<van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
			<van-swipe-item v-for="item in info.banner" :key="item.id"><img class="img" :src="item.image_url" alt="" />
			</van-swipe-item>
		</van-swipe>
		<!-- 头部title导航 -->
		<div class="channelTitle">
			<div @click="tabTitle(item.id)" class="channelList" v-for="item in info.channel" :key="item.id">
				<img :src="item.icon_url" alt="" width="30px" height="30px">
				<p class="titleTxt">
					{{item.name}}
				</p>
			</div>
		</div>

		<!-- 板块1 -->
		<div class="content">
			<p class="contentTitle"> 品牌制造商直供 </p>
			<van-row type="flex" justify="space-between">
				<van-col @click="gobranddetail(item.id)"
				span="12" v-for="item in info.brandList" :key="item.id" class="contenList">
					<p class="contentName">{{item.name}}</p>
					<p class="contentPrice">{{item.floor_price}}起</p>
					<img :src="item.new_pic_url" alt="" class="cententImg">
				</van-col>
			</van-row>
		</div>
		<!-- 新品首发板块 -->
		<div class="newProduct">
			<router-link to="/home/goods?name=new" class="new-txt">
				<p class="newPro">新品首发</p>
				<p class="all">查看全部</p>
			</router-link>
			<img src="../../assets/images/bgnew.png" alt="">
		</div>
		<div class="newgoods">
			<ul class="newgoodsList">
				<li class="newgoodsItem" v-for="item in info.newGoods" :key="item.id" @click="goInfo(item.id)">
					<img :src="item.list_pic_url" alt="" width="100%">
					<h4>{{item.name}}</h4>
					<p>￥{{item.retail_price}}</p>
				</li>
			</ul>
		</div>
		<!-- 人气推荐 -->
		<div class="newProduct">
			<router-link to="/home/goods?name=hot" class="new-txt">
				<p class="newPro">人气推荐 好物精选</p>
				<p class="all">查看全部</p>
			</router-link>
			<img src="../../assets/images/bgtopic.png" alt="">
		</div>
		<div class="newgoods">
			<ul class="newgoodsList">
				<li @click="goInfo(item.id)" class="newgoodsItem" v-for="item in info.hotGoods" :key="item.id">
					<img :src="item.list_pic_url" alt="" width="100%">
					<h4>{{item.name}}</h4>
					<p>￥{{item.retail_price}}</p>
				</li>
			</ul>
		</div>
		<!-- 专题推荐 -->
		<div class="topictitle">
			专题推荐
			<img src="../../assets/images/right.png" alt="" width="14px">
		</div>
		<!-- 分类商品展示 -->
		<div class="topicList">
			<ul class="toplist">
				<li class="topicItem" v-for="item in info.topicList" :key="item.id" @click="godetailaction(item.id)">
					<img :src="item.item_pic_url" alt="">
					<h4>{{item.title}}</h4>
					<h5>{{item.subtitle}}</h5>
					<p>{{item.price_info}} 元起</p>
				</li>
			</ul>
		</div>
		<div class="goodShow">
			<div class="goodShow-list" v-for="item in info.newCategoryList" :key="item.id">
				<p class="goodTitle">{{item.name}}好物</p>
				<div class="goodsList">
					<div class="goodsItem" @click="goInfo(val.id)" v-for="val in item.goodsList" :key="val.id">
						<img :src="val.list_pic_url" alt="">
						<h4>{{val.name}}</h4>
						<p>￥{{val.retail_price}}</p>
					</div>
					<div class="goodsItem last" @click="tabTitle(item.id)"> 
						<h4>{{item.name}}好物
						</h4>
						<p>
							<img src="../../assets/images/rightbig.png" alt="">
						</p>
					</div>
				</div>
			</div>
		</div>
		<div class="sss"></div>
	</div>
</template>

<script>
// 引入接口
	import {
		index
	} from '@/api/home/index';
	export default {
		data() {
			return {
				info: {},
				active: 2,
				city: '',
				obj: {
					location: {
						lng: 113.665412,
						lat: 34.757975
					}
				}
			};
		},
		computed: {},
		created() {
			// 获取首页数据
			index().then(res => {
				this.info = res.data;
				console.log(res.data);
			});

		},
		mounted() {
			var that = this;
			var mapdidian = this.$store.state.mapLocation;
			if (mapdidian.name) {
				that.city = mapdidian.name;
				return;
			}
			AMap.plugin('AMap.CitySearch', function () {
				var citySearch = new AMap.CitySearch()
				citySearch.getLocalCity(function (status, result) {
					if (status === 'complete' && result.info === 'OK') {
						// 查询成功，result即为当前所在城市信息
						that.city = result.city;
					}
				})
			});

			this.$store.commit("upt", this.obj);

		},
		methods: {
			item() {
				console.log(this.info.channel);
			},
			mapCity() {
				this.$store.commit("upt", this.$store.state.mapLocation);
				this.$router.push('/home/map')

			},
			tabTitle(id) {
				this.$router.push(`/category/goods?id=${id}`)

			},
			godetailaction(id) {
				this.$router.push(`/topic/detailaction?id=${id}`)

			},
			goInfo(id){
                this.$router.push(`/category/goods/info?id=${id}`)
			},
			gobranddetail(id){
                this.$router.push(`/home/branddetail?id=${id}`)

			}
		}
	};
</script>

<style scoped lang="scss">
	@import './index.scss';

	.ce {
		position: fixed;
		top: 100px;
		left: 20px;
	}

	#container {
		width: 300px;
		height: 180px;
	}
</style>